body {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

main{
  margin:2rem auto;
  text-align:center;
} 


@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}

button {
  cursor: pointer;
  font: inherit;
  background-color: #7a0144;
  border: 1px solid #7a0144;
  border-radius: 20px;
  padding: 0.5rem 2rem;
  color: white;
}

button:hover,
button:active {
  background-color: #9c095a;
  border-color: #9c095a;
}

img{
  width:300px;
  height:300px;
}